@use '../../utils';
@use './type-utils';
@use './weight';
@use './scale';
@use './tokens.core' as core;

$label1: utils.inherit-map(
  core.$base,
  (
    font-size: type-utils.font-size-relative(label, font-size, '0750'),
    line-height: type-utils.font-size-relative(label, line-height, '1250'),
    letter-spacing: type-utils.calc-letter-spacing(0.5, scale.value('0875'))
  )
) !default;

$label2: utils.inherit-map(
  core.$base,
  (
    font-size: type-utils.font-size-relative(label, font-size, '0813'),
    line-height: type-utils.font-size-relative(label, line-height, '1250'),
    letter-spacing: type-utils.calc-letter-spacing(0.125, scale.value('0813'))
  )
) !default;

$label3: utils.inherit-map(
  core.$base,
  (
    font-size: type-utils.font-size-relative(label, font-size, '0875'),
    line-height: type-utils.font-size-relative(label, line-height, '1250'),
    letter-spacing: type-utils.calc-letter-spacing(0.125, scale.value('0875'))
  )
) !default;

$button: utils.inherit-map(
  core.$base,
  (
    font-size: type-utils.font-size-relative(button, font-size, '0875'),
    font-weight: weight.value(medium),
    letter-spacing: type-utils.calc-letter-spacing(1, scale.value('0875'))
  )
) !default;

$overline: utils.inherit-map(
  core.$base,
  (
    font-size: type-utils.font-size-relative(overline, font-size, '0750'),
    font-weight: weight.value(medium),
    line-height: type-utils.font-size-relative(overline, line-height, '2000'),
    letter-spacing: type-utils.calc-letter-spacing(2, scale.value('0750')),
    text-transform: uppercase
  )
) !default;

$tokens: (
  label1: $label1,
  label2: $label2,
  label3: $label3,
  button: $button,
  overline: $overline
) !default;
